.messaging {
  // With heading variant
  &--with-heading {
    .messaging__description {
      @screen lg2 {
        @apply text-px30 leading-px40;
      }
    }
  }

	&__container {
		@apply tw-grid container mx-auto;

		.messaging.extend-top & {
			@apply pt-px58;

			@screen md {
				@apply pt-px100;
			}

			@screen xl {
				@apply pt-px120;
			}
		}

		.messaging.extend-bottom & {
			@apply pb-px58;

			@screen md {
				@apply pb-px100;
			}

			@screen xl {
				@apply pb-px120;
			}
		}
	}

	&__content {
		@apply col-span-full;
		@apply rounded-px10 py-px60 px-0;
	}

	&__title {
		@apply font-semibold text-px30 leading-px36 text-center;

		@screen md {
			@apply text-px32 leading-px38 tracking-px_05;
		}

		@screen xl {
			@apply text-px40 leading-px46;
		}
	}

	&__header {
		@apply font-normal text-px16 leading-px24 text-center;

		@screen md {
			@apply text-px18 leading-px26 tracking-px05;
		}
	}

	&__description {
		@apply font-semibold text-px24 leading-px28 text-center mt-px14 mx-auto;
    letter-spacing: -0.5px;

    @screen md {
      @apply font-normal text-px26 leading-px36 mt-px18;
      letter-spacing: 0.005em;
      max-width: 494px;
    }

    @screen lg {
      @apply font-normal text-px30 leading-px40 mt-px14;
      max-width: 47.75rem;
    }

		@screen xl {
			@apply text-px26 leading-px36 mt-px14;
		}

		b,
		strong {
			@apply font-semibold;
		}
	}

	&__title + &__description,
	&__title + &__header {
		@apply mt-px16;

		@screen md {
			@apply mt-px15;
		}

		@screen xl {
			@apply mt-px18;
		}
	}

	&__header + &__description {
		@apply mt-px18;

		@screen md {
			@apply mt-px16;
		}
	}

	&__buttons {
		@apply flex flex-wrap flex-shrink-0 gap-5 justify-center items-center;
		@apply text-center mt-px30;

		@screen md {
			@apply flex-wrap;
		}

    .page-btn-link {
      border-bottom-width: 2px;
      border-color: currentColor;
    }
	}

	&__button {
		@apply font-semibold min-h-0 h-auto py-px12 px-px24;
		@apply rounded-px10;

		@screen xl {
			@apply text-px20 leading-px26 tracking-px05 py-px20 px-px40;
		}

		&--link {
			@apply p-0 mt-px2 rounded-none;
		}
	}

	&__title,
	&__header,
	&__description,
	&__buttons {
		@screen md {
			@apply col-span-6 col-start-2;
		}

		@screen xl {
			@apply col-span-8 col-start-2;
		}
	}

  &__large-text {
    @apply text-px20 leading-px26 text-center col-span-full py-px80;

    @screen md {
      @apply col-span-6 col-start-2;
      @apply text-px18 leading-px24 tracking-px_05;
    }

    @screen lg2 {
      @apply text-px26 leading-px36 tracking-em005;
    }

    @screen xl {
      @apply col-span-8 col-start-3 py-px136;
    }
  }
}
